// src/app/(shop)/product/[id]/page.tsx
import Navbar from "@/components/Navbar";
import Image from "next/image";
import type { Product } from "@/lib/products";

async function getProduct(id: string): Promise<Product> {
  const res = await fetch(`http://localhost:3000/api/products/${id}`, { cache: "no-store" });
  if (!res.ok) throw new Error("Product not found");
  return res.json();
}

export default async function ProductPage({ params }: { params: { id: string } }) {
  const product = await getProduct(params.id);

  return (
    <>
      <Navbar />
      <main style={{ maxWidth: 1100, margin: "0 auto", padding: "18px 16px" }}>
        <div style={{ display: "grid", gridTemplateColumns: "1.2fr 1fr", gap: 18 }}>
          <div style={{ borderRadius: 12, overflow: "hidden", border: "1px solid #e5e5e5" }}>
            <Image
              src={product.image}
              alt={product.name}
              width={1200}
              height={900}
              style={{ width: "100%", height: 420, objectFit: "cover" }}
            />
          </div>

          <div>
            <h1 style={{ marginTop: 0 }}>{product.name}</h1>
            <p style={{ opacity: 0.75, marginTop: 6 }}>{product.description}</p>
            <p style={{ fontWeight: 800, fontSize: 20 }}>
              {product.price.toLocaleString("hu-HU")} Ft
            </p>

            <button
              style={{
                padding: "10px 14px",
                borderRadius: 10,
                border: "1px solid #111",
                background: "#111",
                color: "white",
                cursor: "pointer",
              }}
            >
              Kosárba
            </button>
          </div>
        </div>
      </main>
    </>
  );
}
